/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Hanken Grotesk", sans-serif;
  font-size: 1.6rem;
}

*, *:before, *:after {
  box-sizing: inherit;
}

.attribution {
  font-size: 1.1rem;
  text-align: center;
}
.attribution a {
  color: hsl(228, 45%, 44%);
}

.contenedor {
  max-width: 60rem;
  min-width: 50rem;
  margin: 5rem auto;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  box-shadow: 10px 10px 23px -5px rgba(0, 0, 0, 0.2);
  border-radius: 3rem;
}
@media (max-width: 375px) {
  .contenedor {
    grid-template-columns: 1fr;
    height: auto;
    margin: 0 0 5rem 0;
    border-radius: 0 0 3rem 3rem;
    min-width: 100%;
  }
}
.contenedor .result {
  background: linear-gradient(to bottom, hsl(252, 100%, 67%) 0%, hsl(241, 81%, 54%) 70%);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 2rem 5%;
  border-radius: inherit;
  min-width: 25rem;
}
@media (max-width: 375px) {
  .contenedor .result {
    border-radius: inherit;
    padding: 2rem 15%;
  }
}
.contenedor .result h2 {
  text-align: center;
  color: hsl(241, 100%, 89%);
  font-size: 2rem;
  font-weight: 700;
}
.contenedor .result .circle {
  margin: 0 auto;
  width: 20rem;
  height: 20rem;
  border-radius: 50%;
  background: linear-gradient(to bottom, hsl(256, 72%, 46%) 0%, hsla(241, 72%, 46%, 0) 100%);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transform: scale(0.9);
}
@media (max-width: 375px) {
  .contenedor .result .circle {
    transform: scale(0.8);
  }
}
.contenedor .result .circle p {
  font-size: 10rem;
  margin: 0;
  line-height: 1;
  color: hsl(0, 0%, 100%);
  font-weight: 700;
}
.contenedor .result .circle p:last-of-type {
  font-size: 1.8rem;
  color: hsl(241, 100%, 89%);
}
.contenedor .result .content {
  text-align: center;
  margin-bottom: 2rem;
}
.contenedor .result .content h3 {
  margin: 0;
  font-size: 3rem;
  color: hsl(0, 0%, 100%);
}
.contenedor .result .content p {
  font-size: 1.5rem;
  color: hsl(241, 100%, 89%);
}
.contenedor .summary {
  min-width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 2rem;
  min-width: 25rem;
}
.contenedor .summary h2 {
  color: hsl(224, 30%, 27%);
  font-size: 2rem;
  font-weight: 700;
}
.contenedor .summary .item {
  display: grid;
  grid-template-columns: 0.4fr 2fr 1fr;
  align-items: center;
  background: grey;
  border-radius: 1rem;
  padding: 1.5rem 1rem;
  margin: 1.5rem 0;
  transition: all 0.3s ease;
}
.contenedor .summary .item p {
  margin: 0;
  text-align: end;
  color: hsl(224, 30%, 27%);
}
.contenedor .summary .item h4 {
  margin: 0;
}
.contenedor .summary .item span {
  font-weight: 700;
}
.contenedor .summary .item.reaction {
  background-color: #fff0f0;
}
.contenedor .summary .item.reaction h4 {
  color: hsl(0, 100%, 67%);
}
.contenedor .summary .item.memory {
  background-color: #fff8eb;
}
.contenedor .summary .item.memory h4 {
  color: hsl(39, 100%, 56%);
}
.contenedor .summary .item.verbal {
  background-color: #f0fffb;
}
.contenedor .summary .item.verbal h4 {
  color: hsl(166, 100%, 37%);
}
.contenedor .summary .item.visual {
  background-color: #e7eafd;
}
.contenedor .summary .item.visual h4 {
  color: hsl(234, 85%, 45%);
}
.contenedor .summary .item:hover {
  transform: scale(1.1);
}
.contenedor .summary a {
  text-align: center;
  text-decoration: none;
  color: hsl(0, 0%, 100%);
  display: block;
  background: hsl(224, 30%, 27%);
  padding: 1.5rem;
  border-radius: 3rem;
  margin-bottom: 3rem;
}
.contenedor .summary a:hover {
  background: linear-gradient(to bottom, hsl(252, 100%, 67%) 0%, hsl(241, 81%, 54%) 70%);
}