@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenedor {
    max-width: 60rem;
    min-width: 50rem;
    margin: 5rem auto;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    box-shadow: 10px 10px 23px -5px rgba(0,0,0,0.2);
    border-radius: 3rem;
    // height: 50rem;
    @include m.telefono {
        grid-template-columns: 1fr;
        height: auto;
        margin: 0 0 5rem 0;
        border-radius: 0 0 3rem 3rem;
        min-width: 100%;
    }

    .result {
        background: linear-gradient(to bottom, v.$violeta-claro 0%, v.$azul-medio 70%);
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding: 2rem 5%;
        border-radius: inherit;
        min-width: 25rem;
        @include m.telefono {
            border-radius: inherit;
            padding: 2rem 15%;
        }

        h2 {
            text-align: center;
            color: v.$violeta-blanco;
            font-size: 2rem;
            font-weight: v.$bold;
        }

        .circle {
            margin: 0 auto;
            width: 20rem;
            height: 20rem;
            border-radius: 50%;
            background: linear-gradient(to bottom, v.$violeta 0%, v.$azul-oscuro 100%);
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            transform: scale(.9);
            @include m.telefono {
                transform: scale(.8);
            }

            p {
                font-size: 10rem;
                margin: 0;
                line-height: 1;
                color: v.$blanco;
                font-weight: v.$bold;

                &:last-of-type {
                    font-size: 1.8rem;
                    color: v.$violeta-blanco;
                }
            }
        }

        .content {
            text-align: center;
            margin-bottom: 2rem;

            h3 {
                margin: 0;
                font-size: 3rem;
                color: v.$blanco;
            }

            p {
                font-size: 1.5rem;
                color: v.$violeta-blanco;
            }
        }
        
    }

    .summary {
        min-width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding: 2rem;
        min-width: 25rem;

        h2 {
            color: v.$gris;
            font-size: 2rem;
            font-weight: v.$bold;
        }

        .item {
            display: grid;
            grid-template-columns: .4fr 2fr 1fr;
            align-items: center;
            background: grey;
            border-radius: 1rem;
            padding: 1.5rem 1rem;
            margin: 1.5rem 0;
            transition: all .3s ease;

            p {
                margin: 0;
                text-align: end;
                color: v.$gris;
            }

            h4 {
                margin: 0;
            }

            span {
                font-weight: v.$bold    ;
            }

            &.reaction {
                background-color: lighten(v.$rojo, 30%);
                h4 {
                    color: v.$rojo;
                }
            }

            &.memory {
                background-color: lighten(v.$amarillo, 40%);
                h4 {
                    color: v.$amarillo;
                }
            }

            &.verbal {
                background-color: lighten(v.$verde, 60%);
                h4 {
                    color: v.$verde;
                }
            }

            &.visual {
                background-color: lighten(v.$azul, 50%);
                h4 {
                    color: v.$azul;
                }
            }

            &:hover {
                transform: scale(1.1);
            }
        }

        a {
            text-align: center;
            text-decoration: none;
            color: v.$blanco;
            display: block;
            background: v.$gris;
            padding: 1.5rem;
            border-radius: 3rem;
            margin-bottom: 3rem;


            &:hover {
                background: linear-gradient(to bottom, v.$violeta-claro 0%, v.$azul-medio 70%);
            }
        }
    }
    
}